.works {
  margin: 0 auto;
  height: calc(100vh-4rem);
  width: 100vw;
  max-width: 60rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  padding: 0 1rem;
}

.worksTitle {
  margin: 1rem 0;
  font-size: 3rem;
}
.worksDesc {
  font-weight: 300;
  font-size: 1rem;
  max-width: 45rem;
}

.worksImgs {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  width: 100vw;
  max-width: 65rem;
}
.worksImg {
  object-fit: cover;
  height: 14rem;
  margin: 0.5rem;
}

.worksBtn {
  padding: 0.5rem 2.5rem;
  background: white;
  border: none;
  border-radius: 2rem;
  font-size: 1rem;
  margin: 3rem 0;
}

@media screen and (max-width: 585px) {
  .worksImg {
    height: 48vh;
  }
}
